














* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}
.vue-js-app-loading {
  font-size: 30px;
  color: #000;
}
.container {
  min-width: 420px;
  font-family: Arial, Helvetica, sans-serif;
  font-size: 0.9em;
  color: #888;
}
.clear {
  clear: both;
}
#tabs .tabs {
  overflow: hidden;
  background-color: #fff;
  font-size: 14px;
  margin: 0 0 30px 0;
  padding: 0 20px;
}
#tabs .tabs ul {
  list-style-type: none;
  margin-left: 20px;
}
#tabs .tabs a {
  float: left;
  cursor: pointer;
  border-bottom: 2px solid #fff;
  box-shadow: none;
  color: #666;
  display: inline-block;
  margin-right: 30px;
  padding: 20px 0 18px 0;
  text-decoration: none;
}
#tabs .tabs a.active {
  background-color: #fff;
  color: #484848;
  border-bottom: 2px solid #FF982D;
  cursor: default;
}
#tabs .tabs a:hover {
  border-color: #999;
}
#tabs .content .tabcontent .tablenav {
  display: none;
}
#tabs .content .tabcontent #chart_div {
  margin-top: 20px;
}
#tabs .content .tabcontent .refresh-graph {
  text-align: center;
  margin-top: 30px;
}
#tabs .content .tabcontent .refresh-graph button {
  font-size: 20px;
}
.vue-js-app-setting-row {
  padding: 20px 0;
  font-size: 14px;
  line-height: 1.3;
}
.vue-js-app-setting-row p {
  font-size: 14px;
  line-height: 1.3;
}
.vue-js-app-setting-row .vue-js-app-setting-label {
  display: block;
  float: left;
  width: 200px;
  padding: 0 20px 0 0;
}
.vue-js-app-setting-row .vue-js-app-setting-label label {
  display: block;
  font-weight: 600;
  font-size: 1.1em;
}
.vue-js-app-setting-row .vue-js-app-setting-field {
  display: block;
  margin: 0 0 20px 200px;
}
.vue-js-app-setting-row .vue-js-app-setting-field input[type="select"],
.vue-js-app-setting-row .vue-js-app-setting-field input[type="text"],
.vue-js-app-setting-row .vue-js-app-setting-field input[type="email"] {
  background-color: #fff;
  border: 1px solid #ddd;
  border-radius: 3px;
  box-shadow: none;
  color: #333;
  display: inline-block;
  vertical-align: middle;
  padding: 7px 12px;
  margin: 0 10px 0 0;
  width: 400px;
  min-height: 35px;
}
.vue-js-app-setting-row .vue-js-app-setting-field .desc {
  font-style: italic;
  color: #777;
  margin: 8px 0 0;
}
.vue-js-app-button {
  display: inline-block;
  color: #fff;
  text-decoration: none;
  font-size: 13px;
  font-weight: 600;
  margin: 0;
  padding: 8px 16px;
  min-height: 35px;
  background-color: #0085ba;
  border-color: #0073aa #006799 #006799;
  border: 0;
  border-radius: 3px;
  cursor: pointer;
  text-align: center;
  vertical-align: middle;
  white-space: nowrap;
  text-shadow: none;
  box-shadow: none;
  outline: none;
  box-shadow: 0 1px 0 #006799;
}
.vue-js-app-button:hover {
  background: #0073aa;
  border-color: #006799;
  box-shadow: inset 0 2px 0 #006799;
}
.vue-js-app-button.btn-control {
  padding: 3px 14px;
  font-size: 24px;
}
.vue-js-app-button.danger {
  background-color: red;
  border-color: red;
}

ul.vue-js-app-email-list {
  list-style: inherit;
  margin-left: 20px;
}
ul.vue-js-app-email-list li {
  color: #3a3030;
}

.vue-js-app-graph {
  max-width: 700px;
  margin: 20px auto;
}
